@import 'varibles'
@import 'animations'
@import 'darkmode'
@import 'pc_e_lampada'

html 
    scroll-behavior: smooth

body
    margin: 0
    padding: 0
    height: 100vh
    color: var(--cor-texto) 


.container-navbar
    position: fixed
    right: 0
    width: 60%
    height: 50px
    display: flex
    justify-content: flex-end
    align-items: center
   

    & .navbar   
        display: flex
        gap: 30px
        margin-right: 40px
    
    & a
        text-decoration: none
        font-family: $fontItim
        font-size: 1.3em
        color: inherit
        

        &:hover 
            transform: scale(1.2)
            transition: 0.3s ease-in-out

    & a:hover::after
        content: ""
        animation: borda-navbar 0.5s ease
        height: 3px
        background-color: #005431
        display: block
        border-radius: 25px
       


.container-apresentacao
    position: relative
    right: -55%
    width: auto
    max-width: 30%
    height: auto
    white-space: nowrap
    font-size: 3em
    font-family: $fontItim  
    z-index: 1

    & h1
        position: relative
        top: -50px
        right: 50px
        display: block
        animation: fade-in-slide 1.5s ease-out both

.alternar-escrita
    position: relative
    font-size: 3.3vw
    color: #089e7b
    font-weight: bold
    top: -30px
    right: 50px
      
    &::before
            content: "Desenvolvedor"
            font-weight: normal
            color: var(--texto-desenvolvedor)
            margin-right: 10px 
            display: inline-block


.click-container
    position: fixed
    width: 100%
    height: 100%
    pointer-events: none
    z-index: 9999999999999999999999
    top: 0
    left: 0

    .splash
        position: absolute
        animation: fadeOut 2s forwards
        opacity: 1
        transform: scale(1)

        & img
            width: 50px 
            height: 50px 
            pointer-events: none 
            color: #fff







           
  